<template>
  <div class="hot">
    <HoTop />
    <ul>
      <HotSongItem
        @update:music="$emit('update:music', $event)"
        @update:playlist="$emit('update:playlist',hotSongs)"
        v-for="(item, index) in hotSongs"
        :key="index"
        :item="item"
        :index="index"
        :paused="$attrs.paused"
        :currentMusic="$attrs.currentMusic"
      />
    </ul>
  </div>
</template>

<script>
import HotSongItem from "@/components/HotSongItem.vue";
import HoTop from "@/components/HoTop.vue";
export default {
  components: {
    HotSongItem,
    HoTop,
  },
  data() {
    return {
      hotSongs: [],
    };
  },
  created() {
    // console.log("Hot created");
    this.$root.isShowLoading = true;
    this.axios
      .get("/top/list?idx=1")
      .then((response) => {
        this.axios
          .get(
            "/song/detail?ids=" +
              response.data.playlist.trackIds
                .slice(0, 20)
                .map((e) => e.id)
                .join()
          )
          .then((response) => {
            // this.hotSongs=response.data.songs;
            // response.data.songs;
            // console.log(this.hotSongs);
            // console.log(this.hotSongs[11].ar[0].name);
            // console.log(this.hotSongs[11]);
            response.data.songs.forEach((element) => {
              let obj = {
                name: element.name,
                picUrl: element.al.picUrl,
                id: element.id,
                song: {
                  artists: element.ar,
                },
                al: element.al,
              };
              this.hotSongs.push(obj);
            });
            // console.log(this.hotSongs);
            // console.log(response.data.songs);
          })
          .finally(() => {
            this.$root.isShowLoading = false;
          });
      })
      .finally(() => {
        this.$root.isShowLoading = false;
      });
  },
  mounted() {},
};
</script>

